export default {
    name:'center',
    //
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //
        //this.$router.push,history
        //this.$router.replace,history
        //
        this.$router.replace('/user/'+this.loginname);
    }
}



